import { Component } from '@angular/core';
import {
    NavController,
    AlertController, // To Add Button
    ActionSheetController // To delete
} from 'ionic-angular';

import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';

import * as firebase from 'firebase/app';
import { Observable } from '@firebase/util';

@Component({
    selector: 'page-user',
    templateUrl: 'user.html'
})
export class UserPage{
    currentUser: any;
    userRef: any;
    users: AngularFireList<any>;
    seguRef: any;
    segui: AngularFireList<any>;
    

    constructor(
        public navCtrl: NavController,
        public alertCtrl: AlertController,
        public actionSheetCtrl: ActionSheetController,
        public afDatabase: AngularFireDatabase,
        public afAuth: AngularFireAuth
    ){
        this.seguRef = afDatabase.list('segui');
        this.segui = this.seguRef.valueChanges();

        this.userRef = afDatabase.list('users');
        this.users = this.userRef.valueChanges();

        afAuth.authState.subscribe(user => {
            if (!user) {
                this.currentUser = null;
                return;
            }
            this.currentUser = { uid: user.uid, photoURL: user.photoURL };

        });
    }
showOptions(usersID) {
    let actionSheet = this.actionSheetCtrl.create({
        title: '¿Qué quieres hacer?',
        buttons: [
            {
                text: 'Seguir Usuario',
                handler: () => {
                    this.seguidor(usersID);
                }
            }, 
            {
                text: 'Dejar Usuario',
                role: 'destructive',
                handler: () => {
                    this.seguidor(usersID);
                }
            },{
                text: 'Cancel',
                role: 'cancel',
                handler: () => {
                    console.log('Cancel clicked');
                }
            }
        ]
    });
    actionSheet.present();
}

    seguidor(userId: string) {
        const newSeguRef = this.seguRef.push({});
        newSeguRef.set({
            id: newSeguRef.key,
            seguido: userId,
            uid: this.currentUser.uid
        });
}

}


