import { Component } from '@angular/core';
import {
  NavController,
  AlertController, // To Add Button
  ActionSheetController // To delete
} from 'ionic-angular';

import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';
import { Observable } from '@firebase/util';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  currentUser: any;
  songsRef: any;
  songs: AngularFireList<any>;
  
  constructor(
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public actionSheetCtrl: ActionSheetController,
    public afDatabase: AngularFireDatabase,
    public afAuth: AngularFireAuth
  ) {
    this.songsRef = afDatabase.list('songs');
    this.songs = this.songsRef.valueChanges();

    afAuth.authState.subscribe(user => {
      if (!user) {
        this.currentUser = null;
        return;
      }
      this.currentUser = { uid: user.uid, photoURL: user.photoURL };

    });
  }

  addSong() {
    let prompt = this.alertCtrl.create({
      title: 'Publicacion',
      message: "Ingrese publicación",
      inputs: [
        {
          name: 'title',
          placeholder: 'Title',
        }
      ],
      buttons: [
        {
          text: 'Privado',
          handler: data => {
            const newSongRef = this.songsRef.push({});


            newSongRef.set({
              id: newSongRef.key,
              title: data.title,
              estado:'priv',
              uid: this.currentUser.uid
            });
          }
        }, {
          text: 'Mis amigos',
          handler: data => {
            const newSongRef = this.songsRef.push({});


            newSongRef.set({
              id: newSongRef.key,
              title: data.title,
              estado: 'amigo',
              uid: this.currentUser.uid
            });
          }
        },
        {
          text: 'Publico',
          handler: data => {
            const newSongRef = this.songsRef.push({});


            newSongRef.set({
              id: newSongRef.key,
              title: data.title,
              estado: 'publ',
              uid: this.currentUser.uid
            });
          }
        }, {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    prompt.present();
  }

  showOptions(songId, songTitle) {
    let actionSheet = this.actionSheetCtrl.create({
      title: '¿Qué quieres hacer?',
      buttons: [
        {
          text: 'Eliminar publicación',
          role: 'destructive',
          handler: () => {
            this.removeSong(songId);
          }
        }, {
          text: 'Actualizar publicación',
          handler: () => {
            this.updateSong(songId, songTitle);
          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  removeSong(songId: string) {
    this.songsRef.remove(songId);
  }

  updateSong(songId, songTitle) {
    let prompt = this.alertCtrl.create({
      title: 'Publicación',
      message: "Editar publicación",
      inputs: [
        {
          name: 'title',
          placeholder: 'Title',
          value: songTitle
        },
      ],
      buttons: [
        {
          text: 'Privado',
          handler: data => {
            this.songsRef.update(songId, {
              title: data.title, estado:'priv', lastUpdatedBy: this.currentUser.uid
            });
          }
        },
        {
          text: 'Mis amigoss',
          handler: data => {
            this.songsRef.update(songId, {
              title: data.title, estado: 'amigo', lastUpdatedBy: this.currentUser.uid
            });
          }
        },
        {
          text: 'Publico',
          handler: data => {
            this.songsRef.update(songId, {
              title: data.title, estado: 'publ', lastUpdatedBy: this.currentUser.uid
            });
          }
        },
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    prompt.present();
  }

  login() {
    this.afAuth.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider())
      .then((response) => {
        console.log('resultado login google:', response);

        const userRef = this.afDatabase.list('users');
        console.log(userRef);
        userRef.update(response.user.uid,
          {
            userId: response.user.uid,
            displayName: response.user.displayName,
            photoURL: response.user.photoURL,
            email: response.user.email
          });
        //userRef.push({userId: xx.user.uid, displayName: xx.user.displayName}).then((xx)=>{

        //});

      });
  }
  
  loginWithEmail() {
    this.afAuth.auth.signInWithPopup(new firebase.auth.EmailAuthProvider()).then((xx) => {

    });
  }
  logout() {
    this.afAuth.auth.signOut();
  }

}




